import React, { useState, useEffect } from "react";
import "./style.css";
import { Row, Col } from "react-bootstrap";
import { useDispatch,useSelector } from "react-redux";
import ChildCategory from "../CategoryChild";
import { getProductList,toggleProductView } from "../../store/actions/categoryAction.js";
import photos from "../../utils/photos.js";


const Category = ({ category }) => {
  const [photoCounter, setPhotoCounter] = useState(0);
  const dispatch = useDispatch();
  const {toggleProductView:isProductDisplay} = useSelector(state => state.category)
  const findIndexOfNextPhoto = (photos,photoCounter) => {
    let i = (photoCounter + 1) % photos.length;
    while(i !== photoCounter)
        {
         //   alert("dasd");
            if(photos[i])
                {
                    return i;
                }

                i = (i + 1) % photos.length;
        }
        return i;
  }
  console.log("GOORAY!", photoCounter);
  useEffect(() => {
    window.setInterval(() => {
      setPhotoCounter((photoCounter) => {
        return findIndexOfNextPhoto(category.photos, photoCounter);
      });
    }, 3000);
  }, []);
  

  const handleCategoryClick = (id) => {
   dispatch(toggleProductView());
    dispatch(getProductList(id,category,null));
  };

  const showCategoryChilds = (category) => {
    if (!category.child) {
      return;
    }
    const categoryChildComponents = Object.keys(category.child).map((key) => {
      const childCategory = category.child[key];
      childCategory.name = key;
      childCategory.photos = photos[key];
      return (
        <>
            <ChildCategory parentCategory={category} category={childCategory} toggleProductView={toggleProductView}/>
        </>
      );
    });
    return <Row className="category-children">{categoryChildComponents}</Row>;
  };

  return (
    <Col lg={5} className="top-category">
      <div
        className="top-category-image-container"
        style={{ background: `url(${category.photos[photoCounter]})` }}
      >
        <span
          className="top-category-text"
          onClick={(e) => handleCategoryClick(category.id)}
        >
          {category.name}
        </span>
      </div>

      {category.child && (
        <div className="category_child_container">
          {showCategoryChilds(category)}
        </div>
      )}
    </Col>
  );
};

export default Category;
