import React,{useEffect,useState} from "react";
import "./style.css";
import { motion } from "framer-motion";
import { useDispatch, useSelector } from "react-redux";
import {
  addItemToCart,
  removeItemFromCart,
  removeItemFromCartCompletely,
} from "../../store/actions/cartAction.js";
import {
  AddShoppingCart as AddShoppingCartIcon,
  RemoveShoppingCart as RemoveShoppingCartIcon,
  Clear as ClearIcon,
} from "@material-ui/icons/";

const photo = [
  "https://media.wired.com/photos/5e9f574ed42e6f0008f47b7a/master/w_2560%2Cc_limit/Gear-Top-Apple_new-iphone-se-white_04152020.jpg",
];

const Cart = () => {
  const items = useSelector((state) => state.cart.items);
  const dispatch = useDispatch();
  const [totalAmount, setTotalAmount] = useState(0);
  useEffect(() => {
      let totalAmount = 0;
      items.forEach(item => totalAmount += 10 * item.quantity);
      setTotalAmount(totalAmount)
  }, [items])
  const showItems = () => {
    const CartItemComponents = items.map((item) => {
      return (
        <div className="item">
          <div className="cart__item__content" >
            <div
              className="item__photo"
              style={{ background: `url(${photo[0]})` }}
            ></div>
            <span className="ml-2">{item.name}</span>
            <span className="ml-2">X{item.quantity}</span>
            <span className="ml-2">${item.quantity * 10}</span>
          </div>

          <div className="cart__buttons">
            <div className="addcart_container ml-5" onClick={e => dispatch(addItemToCart(item))}>
              <AddShoppingCartIcon style={{ cursor: "pointer" }} />
            </div>
            <div className="removecart_container ml-2" onClick={e => dispatch(removeItemFromCart(item._id))}>
              <RemoveShoppingCartIcon style={{ cursor: "pointer" }} />
            </div>
            <div className="clearitem_container ml-2" onClick={e => dispatch(removeItemFromCartCompletely(item._id))}>
              <ClearIcon style={{ cursor: "pointer" }} />
            </div>
          </div>
        </div>
      );
    });
    return CartItemComponents;
  };
  return (
    <motion.div
      className="cart"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
    >
      <div className="items" style={{display:"flex",flexDirection:"column"}}>
              {showItems()}            
      </div>  
     
      <span>Total Amount is ${totalAmount}</span>
    </motion.div>
  );
};

export default Cart;
