import * as actionTypes from "./actionTypes.js";
import * as categoryApi from "../../api/category.js";

const createAction = (type, payLoad) => {
  return { type, payLoad };
};

export const getCategoryList = () => {
  return async (dispatch) => {
    try {
      dispatch(createAction(actionTypes.SET_FETCHING_CATEGORY));
      const categories = await categoryApi.getCategoryList();
      const categoryList = {};
      categories.forEach((category) => {
       //Category has a parent and the parent is not created yet.  
        if (category.parent && !categoryList[category.parent.name]) {
          categoryList[category.parent.name] = {
              id: category.parent._id,
              items:null,
              child: {
                [category.name]: {
                  id: category._id,
                  photos: category.photos,
                  isActive:category.isActive,
                  items:null,
                  child: null,
              },
            },
          };
          return;
        }
        //category has a parent and the parent has already been created
        if (category.parent && categoryList[category.parent.name]) {
          categoryList[category.parent.name].photos = category.parent.photos;
          categoryList[category.parent.name].child = {
            ...categoryList[category.parent.name].child,
            [category.name]: {
              id: category._id,
              items:null,
              photos: category.photos,
              isActive:category.isActive,
              child: null,
            },
          };
          return;
        }
        //category does not have a parent and category is not created yet
        if (!category.parent) {
          if (!categoryList[category.name]) {
            categoryList[category.name] = {
              id: category._id,
              photos:category.photos,
              isActive:category.isActive,
              items:null,
              child:null,
            };
          }
        }
        //category does not have a parent and category is already created
        if(!category.parent)
            {
                if(categoryList[category.name])
                    {
                        categoryList[category.name].photos = category.photos;
                        categoryList[category.name].isActive = category.isActive;
                    }
            }
      });
      dispatch(createAction(actionTypes.GET_CATEGORY_LIST, categoryList));
    } catch (err) {
      console.log(err);
    }
  };
};

export const getProductList = (id,category,parent) => {
  return async (dispatch) => {
    try{
    dispatch(createAction(actionTypes.SET_FETCHING_PRODUCT_LIST));
    const productList = await categoryApi.getProductList(id);
    dispatch(createAction(actionTypes.GET_PRODUCT_LIST,{productList,category,parent}));
    }
    catch(err)
      {
        console.log(err);
      }
  }
}

export const toggleProductView = () => {
  return createAction(actionTypes.TOGGLE_PRODUCT_VIEW);
}

export const setProductViewFalse = () => {
  return createAction(actionTypes.SET_PRODUCT_VIEW_FALSE);
}
