import React, {useEffect, useState} from "react";
import "./style.css";
import { Row, Col } from "react-bootstrap";
import { useSelector, useDispatch } from "react-redux";
import { getProductList } from "../../store/actions/categoryAction.js";
import Category from "../../components/Category";
import photos from "../../utils/photos.js";


const Categories = ({ toggleProductView }) => {
  const categoryList = useSelector((state) => state.category.categoryList);
  const [photoCounter, setPhotoCounter] = useState(0);
  const dispatch = useDispatch();

  useEffect(() => {
    window.setInterval(
      () => {  
        setPhotoCounter(photoCounter => {
          return photoCounter + 1});

      },1000);
  },[]);

 
 
  const showCategories = () => {
    if (!categoryList) {
      return <></>;
    }

  const CategoryComponents = Object.keys(categoryList).map((key) => {
    const category = categoryList[key];
    category.name = key;
    category.photos = photos[key];
    return (
      <>
        <Category toggleProductView={toggleProductView} category={category}/>
      </>
    );
  });
  return CategoryComponents;
};
  return (
    <div>
      <Row style={{ padding: "40px" }} className="justify-content-center">
        {showCategories()}
      </Row>
    </div>
  );
};

export default Categories;
