const axios = Create({
    baseURL: "https://api.growcify.com/dev/",
  //  timeout: 1000,
  //  headers: {'authorization': "bearer " +  getAuthToken()}
});


export const get = async (url,success,error) => {
        try{
       const {data} = await axios({
        mathod:"get",
        url:url,
        responseType:"application/json",
    });
    success(data);
    }
    catch(err)
        {
         error(err);
        }   
}

export const deleteApi = async (url,success,error) => {
    try{
   const {data} = await axios({
    mathod:"delete",
    url:url,
    responseType:"application/json",
});
success(data);
}
catch(err)
    {
     error(err);
    }   
}


export const post = async(url, reqData, success, error) => {
    try{
        const {data} = await axios({
         mathod:"post",
         url:url,
         data:reqData,
         responseType:"application/json",
     });
     success(data);
     }
     catch(err)
         {
          error(err);
         }  
}

export const put = async(url,reqData,success,error) => {
        try{
            const {data} = await axios({
             mathod:"put",
             url:url,
             data:reqData,
             responseType:"application/json",
         });
         success(data);
         }
         catch(err)
             {
              error(err);
             }  
}
