import React,{useState,useEffect} from "react";
import "./style.css";
import { Row, Col } from "react-bootstrap";
import "./style.css";
import { useDispatch } from "react-redux";
import ChildCategory from "../CategoryChild";
import { getProductList,toggleProductView } from "../../store/actions/categoryAction.js";

const CategoryChild = ({category,toggleProductView,parentCategory}) => {

    const [photoCounter, setPhotoCounter] = useState(0);
    const dispatch = useDispatch();
  
    const findIndexOfNextPhoto = (photos,photoCounter) => {
      let i = (photoCounter + 1) % photos.length;
      while(i !== photoCounter)
          {
      
              if(photos[i])
                  {
                      return i;
                  }
  
                  i = (i + 1) % photos.length;
          }
          return i;
    }
    
    useEffect(() => {
      window.setInterval(() => {
        setPhotoCounter((photoCounter) => {
          return findIndexOfNextPhoto(category.photos, photoCounter);
        });
      }, 3000);
    }, []);
   
    const handleCategoryClick = (id) => {
      dispatch(toggleProductView());
      dispatch(getProductList(id,category,parentCategory));
    };

    
    return (
        <Col className="category-child" >
          <div className= "category-child-image-container" style={{ background: `url(${category.photos[photoCounter]})` }}>
                <span className="category-child_text" onClick={e => handleCategoryClick(category.id)}>{category.name}</span>
           </div>           
        </Col>
    );
}

export default CategoryChild;