import React,{useState,useEffect} from "react";
import "./style.css";
import {Col} from "react-bootstrap";
import {useDispatch} from "react-redux";
import {addItemToCart,removeItemFromCart,removeItemFromCartCompletely} from "../../store/actions/cartAction.js";
import {ArrowLeft as ArrowLeftIcon, ArrowRight as ArrowRightIcon} from "@material-ui/icons";
import {motion,useAnimation} from "framer-motion";

const photos =   ["https://5.imimg.com/data5/GR/CA/XR/SELLER-28406699/1-25l-maaza-mango-juice-500x500.jpg","https://media.wired.com/photos/5e9f574ed42e6f0008f47b7a/master/w_2560%2Cc_limit/Gear-Top-Apple_new-iphone-se-white_04152020.jpg","https://solidstarts.com/wp-content/uploads/Mango_edited-480x320.jpg"];
const Product = ({product}) => {
    const [isHovering,setIsHovering] = useState(false);
    const leftArrowController = useAnimation();
    const rightArrowController = useAnimation();
    const [photoCounter,setPhotoCounter] = useState(0);

    const showNextPhoto = () => {
        setPhotoCounter((photoCounter + 1) % photos.length );
    }

    const showPrevPhoto = () => {
        if(photoCounter === 0)
            {
                return;
            }
        setPhotoCounter(photoCounter  - 1); 
    }
    const showArrow = () => {
        leftArrowController.start({x:0});
        rightArrowController.start({x:0});
    }

    const hideArrow = () => {
        leftArrowController.start({x:-100});
        rightArrowController.start({x:100});
    }

    useEffect(() => {
        if(isHovering)
            {
              showArrow();
              return;  
            }
            hideArrow();

    },[isHovering]);
    console.log(photoCounter);
    const dispatch = useDispatch();
    return (<Col lg={3} className="product ml-3 mb-5" onMouseEnter={e => setIsHovering(true)} onMouseLeave={e => setIsHovering(false)}>
        <div className="product__photo" style={{background:`url(${photos[photoCounter]})`,width:"100%",height:"80%"}}>
            <motion.div className="left-arrow-container" initial={{x:-120}} animate={leftArrowController} onClick={e => showPrevPhoto()}>
                <ArrowLeftIcon style={{height:"75px",width:"75px"}}/>
            </motion.div>
            <motion.div className="right-arrow-container" initial={{x:120}} animate={rightArrowController} onClick={e => showNextPhoto()}>
                <ArrowRightIcon style={{height:"75px",width:"75px"}}/>
            </motion.div>
        </div>
        <span style={{fontSize:"2rem"}}>{product.name} </span>
        <span style={{fontSize:"1.2rem"}}>Price: {10}$</span> 
        <button onClick={e => dispatch(addItemToCart(product))} className="product__add-to-cart-button">Add to Cart</button>
    </Col>)
}

export default Product;