import React, { useState, useEffect } from "react";
import { motion, useAnimation } from "framer-motion";
import {ShoppingBasket as ShoppingBasketIcon} from '@material-ui/icons/';
import {useDispatch,useSelector} from "react-redux";
import "./style.css";
import {showCartDropdown,hideCartDropdown} from "../../store/actions/cartAction.js";
import Cart from "../Cart";
import Scroll from "react-scroll";

const Header = ({show,elementName}) => {
    const dispatch = useDispatch();
    const headerController = useAnimation();
    const {isDropdownHidden,items }= useSelector(state => state.cart);
    const INITIAL_YPOS = -100;
    const scroller = Scroll.scroller;
    const scroll = Scroll.animateScroll;
    const slideDownHeader = async () => {
        await headerController.start({y:0,transition:{duration:1}});
    }
    const countNoOfItems = () => {
      if(items)
        {
          let i = 0;
          items.forEach(item => i+= item.quantity );
          if(i > 100)
            {
              return "100+";
            }
          return i;
        }

    }
    const slideUpHeader = async () => {
        await headerController.start({y:INITIAL_YPOS,transition:{duration:1}});
    }

    useEffect(() => {
        if(!show)
        {   
            slideUpHeader();
            return;
        }
     slideDownHeader();
    }, [show]);

  return (
    <motion.header
      className="app__header app__header__shadow"
      initial={{ y: INITIAL_YPOS}}
      animate={headerController}
    >
      <span className="app__header__logo">Growcify</span>
      <div className="app__header__buttons">
        <span className="mr-5 app__header__buttons__button" onClick={e => {
          scroll.scrollToTop({
            duration: 800,
            delay: 100,
            smooth: true,
           offset: -50, 
          });
        }}>Top</span>
        <span className=" app__header__buttons__button"  onClick={(e) => {
              scroller.scrollTo(elementName, {
                duration: 800,
                delay: 100,
                smooth: true,
               offset: -50, 
              });
            }}>Browse</span>
        <div onMouseEnter={
          e  => {
            dispatch(showCartDropdown());
          }
        } onMouseLeave={e => {
          dispatch(hideCartDropdown());
        }}
        style={{position:"relative"}}
        >
             <ShoppingBasketIcon className="ml-5" style={{height:"25px",width:"25px",cursor:"pointer"}} />
            {!isDropdownHidden && <Cart />}
            {items.length !== 0 && <span className="item-number">X{countNoOfItems()}</span>}

        </div>
     
      </div>
    </motion.header>
  );
};

export default Header;
