import React from "react";
import "./style.css";
import {useSelector,useDispatch} from "react-redux"; 
import {ArrowRight as ArrowRightIcon} from '@material-ui/icons/';
import Product from "../../components/Product";
import {Row} from "react-bootstrap";
import { toggleProductView,getProductList } from "../../store/actions/categoryAction.js";

const Products = () => {
    const {currentProductList:productList, currentCategory:categoryList} = useSelector(state => state.category);
    const dispatch = useDispatch();
    const showCategories = () => {
        if(!categoryList || !productList)
            {
                return;
            }
        const CategoryNameComponents = [];
        for(let i = categoryList.length-1; i >= 0; i--)
            {
              
                CategoryNameComponents.push(
                    <div key={i} style={{display:"flex",alignItems:"center"}}>
                    <ArrowRightIcon style={{height:"60px", width:"60px",color:"black"}} />
                    <span className="ml-2 products__category" onClick={e => dispatch(getProductList(categoryList[i].id,categoryList[i],categoryList[i].parent))}>
                        {categoryList[i].name}
                    </span>
                    </div>
                )
            }
            return CategoryNameComponents;
    }

    const showProducts = () => {
        if(!productList)
            {
                return;
            }
        const ProductComponents = productList.map((product,index) => {
               return(<Product key={index} product={product} />) 
        });

        return ProductComponents;
    }

     return (<div className="products">
         <div className="products__header">
                <span className="products__category" onClick={e => dispatch(toggleProductView())}>Categories</span>
               {showCategories()}
         </div>
        <Row className="products__content">
                {showProducts()}
        </Row> 
     </div>);  
}
export default Products;