import React, {useEffect} from "react";
import "./style.css";
import { motion, useAnimation } from "framer-motion";
import { LocalShipping as ShippingIcon,Fastfood as FastFoodIcon,Adjust as AdjustIcon,Help as SupportIcon } from "@material-ui/icons";
const Services = () => {
  const INITIAL_SERVICES_YPOS = 100;
  
  const services1C = useAnimation();
  const services2C = useAnimation();
  const services3C = useAnimation();
  const services4C = useAnimation();

  const slideUpServices = async() => {
    await services1C.start({y:0,opacity:1,transition:{duration:0.2}});
    await services2C.start({y:0,opacity:1,transition:{duration:0.2}});
    await services3C.start({y:0,opacity:1,transition:{duration:0.2}});
    await services4C.start({y:0,opacity:1,transition:{duration:0.2}});
  }
  useEffect(() => {
    slideUpServices();
  }, []);
  return (
    <section className="services">
      <motion.div className="services__circle__container" initial={{y:INITIAL_SERVICES_YPOS, opacity:0}} animate={services1C}>
        <div className="services__circle__1 services__circle">
          <div className="services__circle__inner">
            <ShippingIcon
              style={{ height: "50px", width: "50px", color: "white" }}
            />
          </div>
        </div>
        <span style={{ fontWeight: "bolder" }} className="mt-2">
          FREE SHIPPING
        </span>
        <span style={{ fontSize: "0.8rem" }}>ON ORDER OVER $100</span>
      </motion.div>

      <motion.div className="services__circle__container" initial={{y:INITIAL_SERVICES_YPOS, opacity:0}} animate={services2C}>
        <div className="services__circle__2 services__circle">
          <div className="services__circle__inner">
            <FastFoodIcon
              style={{ height: "50px", width: "50px", color: "white" }}
            />
          </div>
        </div>
        <span style={{ fontWeight: "bolder" }} className="mt-2">
          FREE SHIPPING
        </span>
        <span style={{ fontSize: "0.8rem" }}>ON ORDER OVER $100</span>
      </motion.div>

      <motion.div className="services__circle__container" initial={{y:INITIAL_SERVICES_YPOS, opacity:0}} animate={services3C}>
        <div className="services__circle__3 services__circle">
          <div className="services__circle__inner">
            <AdjustIcon
              style={{ height: "50px", width: "50px", color: "white" }}
            />
          </div>
        </div>
        <span style={{ fontWeight: "bolder" }} className="mt-2">
          FREE SHIPPING
        </span>
        <span style={{ fontSize: "0.8rem" }}>ON ORDER OVER $100</span>
      </motion.div>

      <motion.div className="services__circle__container" initial={{y:INITIAL_SERVICES_YPOS, opacity:0}} animate={services4C}>
        <div className="services__circle__4 services__circle">
          <div className="services__circle__inner">
            <SupportIcon
              style={{ height: "50px", width: "50px", color: "white" }}
            />
          </div>
        </div>
        <span style={{ fontWeight: "bolder" }} className="mt-2">
          FREE SHIPPING
        </span>
        <span style={{ fontSize: "0.8rem" }}>ON ORDER OVER $100</span>
      </motion.div>


    </section>
  );
};

export default Services;
