import React, { useState, useEffect } from "react";
import "./style.css";
import Carousel from "react-bootstrap/Carousel";
import Scroll from "react-scroll";

const RCarousel = ({ elementName }) => {
  const [activeIndex, setActiveIndex] = useState(0);

  const switchImgFunc = () => {
    setActiveIndex((activeIndex) => (activeIndex ? 0 : 1));
  };
  const scroller = Scroll.scroller;
  useEffect(() => {
    const id = window.setInterval(() => {
      switchImgFunc();
    }, 4000);

    const willUnmount = () => {
      window.clearInterval(id);
    };
    return willUnmount;
  }, []);

  return (
    <Carousel
      activeIndex={activeIndex}
      style={{ maxHeight: "80vh", background: "orange", position: "relative" }}
      fade
      indicators={false}
      nextIcon={""}
      prevIcon={""}
    >
      <Carousel.Item style={{ maxHeight: "80vh" }}>
        <img
          className="d-block w-100"
          src="./images/c-1.jpg"
          alt="First slide"
        />
      </Carousel.Item>
      <Carousel.Item style={{ maxHeight: "80vh" }}>
        <img
          className="d-block w-100"
          src="./images/c-2.jpg"
          alt="Second slide"
        />
      </Carousel.Item>
      <div className="caraousel__block">
        <div className="caraousel__block__content">
          <span className="caraousel__block__text">Growcify</span>
          <button
            className="caraousel__block__button"
            onClick={(e) => {
              scroller.scrollTo(elementName, {
                duration: 800,
                delay: 100,
                smooth: true,
               offset: -50, // Scrolls to element + 50 pixels down the page
              });
            }}
          >
            Browse
          </button>
        </div>
      </div>
    </Carousel>
  );
};

export default RCarousel;
