import React, { useState, useEffect, useRef } from "react";
import { motion, useAnimation } from "framer-motion";
import "./index.css";
import Header from "../../components/Header";
import Carousel from "../../components/Carousel";
import {useDispatch,useSelector} from "react-redux";
import {getCategoryList,setProductViewFalse} from "../../store/actions/categoryAction.js";
import Services from "../../components/Services";
import Categories from "../../components/Categories";
import Products from "../../components/Products";
import LoadingCircle from "../../components/LoadingCircle";
import Scroll from "react-scroll";
import Footer from "../../components/Footer";

const Home = () => {
  const [displayHeader, setDisplayHeader] = useState(false);
  const [isShowingCategory, setIsShowingCategory] = useState(true);
  const dispatch = useDispatch();
  const browseSectionRef = useRef(null);
  const {isFetchingProductList,isFetchingCategoryList,toggleProductView} = useSelector(state => state.category);
  const Element = Scroll.Element;
  const shouldHeaderDisplay = () => {
      if(window.pageYOffset <= 50)
        {
          setDisplayHeader(false);
          return;
        }
        setDisplayHeader(true);
  }

  useEffect(() => {
     dispatch(getCategoryList());
     dispatch(setProductViewFalse());
     window.addEventListener("scroll", shouldHeaderDisplay);
     const willUnmount = () => {
      window.removeEventListener("scroll",shouldHeaderDisplay);
     }
     return willUnmount;
  }, []);

  const showProductsOrCategories = () => {
      if(isFetchingCategoryList || isFetchingProductList)
        {
          return <LoadingCircle />
        }
      return !toggleProductView ?  <Categories/> : <Products />
  }
 
  return (

    <div style={{}}>
        <Header show={displayHeader} elementName="browse" />
        <Carousel elementName="browse"/>
        <Services />
        <Element name="browse"/>
        <div className="home__content" style={isFetchingCategoryList || isFetchingProductList ? {marginBottom:"500px"} : {}}>
            {showProductsOrCategories()}
        </div>
        <Footer />
    </div>
   
 
  );
};

export default Home;
